.btn {
  box-sizing: border-box;
  border-radius: 5px;
  color: #232833;
  font-size: 16px;
  line-height: 22px;
  min-height: 50px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  font-weight: 700;
  transition: 0.4s;
}
.btn i {
  padding-left: 11px;
  column-rule: inherit;
  font-size: 14px;
  transition: 0.4s;
}
.btn:hover i {
  transform: translateX(15%);
}
.btn-primary {
  border: 1px solid #FE8083;
  background-color: #FE8083;
  color: #fff;
}
.btn-secondary {
  border: 1px solid #FE8083;
  background-color: #FE8083;
}
.btn-accent {
  border: 1px solid #FFEEEE;
  background-color: #FFEEEE;
  color: #FE8083;
}
.btn-accent:hover {
  background-color: #FE8083;
  color: #fff;
}
.btn-outline {
  border: 1px solid #D7E0E7;
  background-color: transparent;
  color: #092C4C;
  transition: 0.4s;
}
.btn-outline:hover {
  background-color: #FE8083;
  color: #fff;
}
.btn-outline i {
  margin-right: 10px;
}

.site-header .nav-item .nav-link {
  border-bottom: 2px solid transparent;
}
.site-header .nav-item .nav-link.active, .site-header .nav-item .nav-link:hover {
  border-bottom: 2px solid #FE8083;
}

.slick-slider .slick-slide img {
  width: 100%;
}
.slick-slider .slick-arrow {
  background-color: #536476;
  border: none;
  font-size: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.slick-slider .slick-arrow:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  opacity: 0.75;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.slick-slider .slick-arrow.slick-prev {
  left: -15px;
}
.slick-slider .slick-arrow.slick-prev:before {
  content: "←";
}
.slick-slider .slick-arrow.slick-next {
  right: -15px;
}
.slick-slider .slick-arrow.slick-next:before {
  content: "→";
}
.slick-slider .slick-arrow:hover {
  background-color: #3e4b58;
}

